@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --fill: #ff5454;
    --stroke: black;
}

@supports (font-variation-settings: normal) {
    html > * {
        font-family: 'Inter var', sans-serif;
        color: var(--night);
    }
    input {
        font-family: 'Inter var', sans-serif;
    }
}   

.App {
  display: grid;
  justify-items: center;
  height: 100vh;
  width: 100vw;
  align-items: center;
  align-content: center;
}

svg {
    width: 100%;
    stroke-width: 3.5;
}

path {
    stroke: var(--stroke);
    fill: var(--fill);
}

circle {
    fill: var(--fill);
}

.closed, .pointing {
    cursor: pointer;
}

.pointing :nth-child(2) {
    opacity: 0;
}
.closed :nth-child(1) {
    opacity: 0;
}

circle.lowlight {
    opacity: 0;
    transition: opacity 100ms, transform 100ms, stroke-width 100ms;
    transform: translateY(10%);
}

circle.highlight {
    transform: translateY(0%);
    opacity: 1;
}

text {
    stroke-width: 0;
    fill: black;
}

.keys {
    display: flex;
}

.keys > * {
    white-space: nowrap;
    flex: 1 1;
    text-align: center;
}
article {
    display: flex;
}

article > * {
    max-width: 320px;
}

footer {
    grid-area: footer;
    text-align: right;
}

[type="range"] {
    width: 100%;
}

[type="number"] {
    margin-left: 10px;
    width: 75px;
}
